import Login from "./Login";
import { BrowserRouter, HashRouter , Route } from 'react-router-dom';
import SignUp from "./SignUp";
import Home from "./Home";
function App() {
    return (
        <BrowserRouter>
            <HashRouter >
                <Route path="/OnlineAcademyFE" element={<Login/>} />
                <Route path="/signup" element={<SignUp />} />
                <Route path="/OnlineAcademyFE/home" element={<Home />} />
            </HashRouter > 
        </BrowserRouter>
    );
}

export default App;